import React from "react"
import "./Demo1.css"
import Paging from "./Paging";

export default class Demo1 extends React.Component{
    constructor(){
        super()
    }
    componentDidMount(){
        this.initUserToTable();
    }
    initUserToTable(){
        fetch("http://localhost:3000/test.json").then((res)=>{
            return res.json()
        }).then((data)=>{
            console.log(data);
            for(var i=0;i<data.length;i++){
                if(i<5){
                    var oTr=document.createElement("tr");
                    console.log(data[i])
                    
                    for(var key in data[i]){
                        var oTd=document.createElement("td");
                        oTd.innerHTML=data[i][key];
                        var frag1=document.createDocumentFragment();
                        frag1.appendChild(oTd);
                        oTr.appendChild(frag1);
                    }
                    
                    var frag=document.createDocumentFragment();
                    frag.appendChild(oTr);
                    var oTbody=document.querySelector("table.userTable>tbody");
                    oTbody.appendChild(frag); 
                }
            }
        }
            )
    }
    render(){
        return(
            <div id="demo1">
                <div className="demo1">A</div>
                <div className="searchBox"><div className="btn"></div><input type="text"/><button className="reload" ></button></div>
                <div className="tableBox">
                
                <table className="userTable">
                        <thead>
                        <tr>
                        <th></th><th></th><th> </th><th>/ </th><th></th>
                        <th></th><th></th><th></th><th></th><th></th>
                        </tr>
                        </thead>
                        <tbody>
                        
                        
                        
                        </tbody>
                </table>
                
                <Paging/>
                </div>
                
            </div>
        )
    }
}

