import React from "react"
import "./Paging.css"
export default class Paging extends React.Component{
    constructor(){
        super()
    }
    render(){
        return (
            <div id="paging">

                <div>
                    <span>&lt;&lt;</span>
                    <span>1</span>
                    <span>&gt;&gt;</span>
                </div>

                <div>
                    <button></button>
                </div>

                <div>
                    <span></span><input type="text" value="1"/><span></span>
                </div>
                
                <div><span>2</span>,<span>1</span>/<span>1</span></div>

            </div>
        )
    }
}
